.listing-item
{
    display:    block;
}

.listing-item > img, .listing-item > video
{
    float:      left;
    margin-right: 8px;
    border-radius: 5px;
}

.listing-item > video {
    width:      100%;
    max-height: 480px;
}

.listing-item::after
{
    content:    '';
    display:    block;
    clear:      both;
}

.listing-item-image {
    width: 100%;
    max-height: 640px;
}

.listing-item-image img {
    object-fit: contain;
    width: auto;
    max-width: 100%;
    max-height: 480px;
}
